<template>
    <div class="main">
    <Dashboar />
    <div class="container">
        <h1>Gestion tickets</h1>
      <div class="control"> 

      <input type="text" v-model="search" class="input is rounded" @keyup="searchData" />
       <button class="success" @click="searchData"> Buscar </button>      
      </div>
        <div class="item-resul" >
        <table class="table">
               <thead>
                   <tr>
                       <th scope="col">Placa</th>
                       <th scope="col">Entrada</th>
                       <th scope="col">puesto</th>
                       <th scope="col">vehiculo</th>
                       <!-- <th scope="col">tiempo</th> -->
                       <th scope="col">Pagar</th>
                       <th scope="col">Acciones</th>
                   </tr>
               </thead>
               <tbody>
                   <tr  >
                       <th scope="row">{{ticket.placa}}</th>
                       <td >{{ticket.tiempoInicio}} </td>
                       <td>{{ticket.Puesto}}</td>
                       <td>{{ticket.TipoVehiculo}}</td>
                       <!-- <td>{{ ticketeditar.tiempoFinal }}</td> -->
                       <!-- <td v-if="ticketeditar.valorP=true">0</td> -->
                       <td> ${{ticketeditar.valorP}} </td>
                   
                       <!-- <td><button type="button" @click="finTiempo" class="btn btn-danger">Finalizar tiempo</button> -->
                       <td> <button type="button" @click="valorPagar" class="btn btn-warning">Valor a pagar</button> 
                        </td>
                   </tr>
               </tbody>
 
            </table>   
</div>
<button style="width:100%;" type="button" @click="finTicket" class="btn btn-danger my-3">Finalizar ticket</button>
                  <b-alert :show="dismissCountDown" 
                dismissible
                fade
                :variant="mensaje.color"
                @dismissed="dismissCountDown=0"
                @dismiss-count-down="countDownChanged"
                >
               {{mensaje.texto}} 
          </b-alert>
        </div>
        </div>
</template>
<script>
// import api from '@/api';
import Dashboar from './Dashboar.vue'
// import moment from 'moment'
// import ViewTicket from './ViewTicket.vue';
// import Users from '@/components/Users.vue';

export default {
//   name: 'App',
  data(){
      return{
          // props: [ticket],
          ticketeditar: { tiempoFinal: Date() ,valorP: 0 },
          // viewPago: false,
          tickets: [],
          search: '',
          ticket: {
 
            "placa": "",
            "tiempoInicio": "" ,
            "TipoVehiculo": "",
            "Puesto": "",
            "tiempoFinal": "",
            "valorP": ""
            
          },
         mensaje: {color: '', texto: '' ,tiempo:Date()},
        //  mensaje2: {}
         dismissSecs: 10000,
         dismissCountDown: 0,
         showDismissibleAlert: false,
          diffe: Number,
          star: '',
          end: '',
          min: Number,
          
        //   }
      }
  },
  components: {
      Dashboar,
  },
  created(){
},
  methods:{
//   mostralist(){
//       this.axios.get('/tickets')
//         .then(res => {
//         console.log(res.data)
//         this.tickets = res.data
//           })
//         .catch(e => {
//         console.log(e.response);
//         })
//   },
    searchData(){
        this.axios.get('/tickets/placa/' + this.search)
        .then(res => {
            this.ticket = res.data
        this.ticket.placa = res.data[0].placa;
        this.ticket.tiempoInicio = res.data[0].tiempoInicio;
        this.ticket.TipoVehiculo = res.data[0].TipoVehiculo;
        this.ticket.Puesto= res.data[0].Puesto;
        this.ticket.tiempoFinal= res.data[0].tiempoFinal;
        console.log(this.ticket)
          })
        .catch(e => {
        console.log(e.res);
        })
  },
  finTiempo(){
    
      this.axios.put('/tickets/placa/' + this.search , this.ticketeditar)
      .then(res => {
          this.ticketeditar=res.data.tiempoFinal
           this.ticketeditar=res.data.valorPagar
          console.log(res)
          this.mensaje.color='danger',
          this.mensaje.texto= 'Tiempo finalizado ' + this.mensaje.tiempo,
          this.showAlert()
          setTimeout(location.reload(),10000)
      })
      .catch(e=>{
    console.log(e);
      })
  },
     async valorPagar(){
      //  this.viewPago = true
         this.end =  new Date(this.ticket.tiempoFinal)
         this.star = new Date(this.ticket.tiempoInicio)
         this.diffe = await Math.abs(this.end-this.star)
         this.min = Math.floor((this.diffe/1000)/60) 
         if (this.ticket.TipoVehiculo === 'Camion' && this.ticket.TipoVehiculo ==='Carro' && this.ticket.TipoVehiculo === 'Camionetas' && this.ticket.TipoVehiculo === 'Vans' ){
             this.ticketeditar.valorP =  this.min * 75
         }else{
             this.ticketeditar.valorP = this.min * 52
         }
          this.mensaje.color='warning',
          this.mensaje.texto='valor a pagar',
          this.showAlert()
          
         console.log(this.min)
         
    },
    finTicket(){
        this.axios.put('/tickets/placa/' + this.search , this.ticketeditar)
      .then(res => {
          this.ticketeditar=res.data.tiempoFinal
          //  this.ticket.valorP=res.data.valorP
          console.log(res)
          this.mensaje.color='success',
          this.mensaje.texto= 'Ticket Finalizado' + this.mensaje.tiempo,
          this.showAlert()
          setTimeout(location.reload(),15000)
      })
      .catch(e=>{
    console.log(e);
      })
    },

    countDownChanged(dismissCountDown) { this.dismissCountDown = dismissCountDown
         },
    showAlert() { this.dismissCountDown = this.dismissSecs },
   }
}
</script>

<style scoped>
.container{
    height: 600px;
}
.ticket{

    /* width: 100%; */
}
.item-resul{
    margin-top: 25px;
    text-align: center;
    width: 100%;
    border: medium double #369; 
}
p{   
    width: 100%;
    text-align: center;
}

</style>